body {
  font-family: Montserrat, sans-serif;
  font-size: 16px;
}

.movies-container {
  grid-template-columns: 1fr;
  gap: 20px;
  display: grid;
}

@media (min-width: 768px) {
  .movies-container, #gallery-container {
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    display: grid;
  }
}

@media (min-width: 1280px) {
  .movies-container, #gallery-container {
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    display: grid;
  }
}

.movie-card {
  text-align: center;
}

.movie-image {
  width: 100%;
  cursor: pointer;
}

